import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { AuthGuard } from './modules/core/auth.guard';

const routes: Routes = [
  {
    path: 'user',
    loadChildren: './modules/user/user.module#UserModule'
  },
  {
    path: 'dashboard',
    loadChildren: () => import('./modules/dashboard/dashboard.module').then(m => m.DashboardModule),
    data: { 
      breadcrumb: 'Dashboard'
    },
    canLoad: [AuthGuard]
  },
  // {
  //   path: 'diagnoses',
  //   loadChildren: () => import('./modules/diagnose/diagnose.module').then(m => m.DiagnoseModule),
  //   canLoad: [AuthGuard]
  // },
  {
    path: '',
    redirectTo: '/dashboard',
    pathMatch: 'full'
  },
  { path: 'patients', loadChildren: () => import('./modules/patients/patients.module').then(m => m.PatientsModule) },
  // { path: 'diagnoses', loadChildren: () => import('./modules/diagnoses/diagnoses.module').then(m => m.DiagnosesModule) },
  { path: 'users', loadChildren: () => import('./modules/user/user.module').then(m => m.UserModule) }
];
@NgModule({
  imports: [RouterModule.forRoot(routes)],
  // providers: [AuthGuard],
  exports: [RouterModule]
})
export class AppRoutingModule { }
